
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapState } from 'vuex'
import { AjaxHttp, AjaxSilentHttp } from '../../../js/ajaxHttp'
import ListCell from '../../crm/components/ListCell.vue'
import { getYMDFormateTime, getYMDHMFormateTime, isArrayType, getImageUrl, openFile } from "../../../js/utility.js"

export default {
components: {
Tab,
TabItem,
Actionsheet,
Group,
Cell,
ListCell
},
data() {
return {
logDetail: '',
showRightMenus: false,
rightMenus: {},
nav_type: 'reply',
praiseImage: require("../../../assets/common_praise_light_normal.png"),
}
},
computed: {
...mapState({
logMDetail: state => state.logDetail,
logListInfo: state => state.logListInfo,
}),
logContent() {
var content = this.logDetail.content;
return content.replace(/\r\n/g, "<br>");
}
},
beforeRouteLeave(to, from, next) {

if (to.name === 'filepreview' || to.name === 'createlog' || to.name === 'user' || to.name === 'createcomment') { //
this.logDetail.refreshComment = false; 
this.$store.commit('updateLogDetail', this.logDetail);
} else { //
this.$store.commit('updateLogDetail', '');
}
next();
},
mounted() {
if (this.logMDetail !== '') {
this.logDetail = this.logMDetail;
this.nav_type = this.logMDetail.nav_type;
this.getPraiseImage();
if (this.logDetail.permission.edit === 1) {
this.rightMenus.edit = '';
}
if (this.logDetail.permission.delete === 1) {
this.rightMenus.delete = '';
}

if (this.logMDetail.refreshComment) {
this.getSilentDetail();
}
} else {
this.getDetail();
}

},
methods: {
getDetail() {
AjaxHttp('POST', 'm=log&a=mylog_view', { id: this.$route.params.id }, this, function(res, wself, success) {
if (success) {
wself.handleInfo(res);
}
});
},
getSilentDetail() {
AjaxSilentHttp('POST', 'm=log&a=mylog_view', { id: this.$route.params.id }, this, function(res, wself, success) {
if (success) {
wself.handleInfo(res);
}

});
},
handleInfo(res) {
var detail = res.data;
detail.img = getImageUrl(detail.img);
detail.create_date = getYMDFormateTime(detail.create_date);
var comment_count = detail.comment_count;
var praise_count = detail.praise_count;
detail.comment_count = comment_count === '0' ? '' : '' + '' + comment_count + '';
detail.praise_count = praise_count === '0' ? '' : '' + '' + praise_count + '';
detail.b_comment_count = comment_count === '0' ? '' : comment_count;
detail.b_praise_count = praise_count === '0' ? '' : praise_count;

this.logDetail = detail; 
this.logDetail.permission = res.permission; 


var praise_list = [];
for (var i = 0; i < detail.praise_list.length; i++) {
var item = detail.praise_list[i];
item.user_info.thumb_path = getImageUrl(item.user_info.thumb_path);
praise_list.push(item);
}
this.logDetail.praise_list = praise_list;

var comment_list = [];
for (var i = 0; i < detail.comment_list.length; i++) {
var item = detail.comment_list[i];
item.img = getImageUrl(item.img);
item.create_time = getYMDHMFormateTime(item.create_time);

for (var j = 0; j < item.comment_list_child.length; j++) {
var childitem = item.comment_list_child[j];
childitem.creator_child.thumb_path = getImageUrl(childitem.creator_child.thumb_path);
childitem.create_time = getYMDHMFormateTime(childitem.create_time);
}
comment_list.push(item);
}
this.logDetail.comment_list = comment_list;

this.logDetail.nav_type = 'reply'; 
this.nav_type = 'reply';
this.getPraiseImage();
if (this.logDetail.permission.edit === 1) {
this.rightMenus.edit = '';
}
if (this.logDetail.permission.delete === 1) {
this.rightMenus.delete = '';
}
},
fileTypeImage(item) {
var type = item.file_type;
if (type === 'doc' || type === 'DOC' || type === 'docx') {
return require("../../../assets/file/doc.png");
} else if (type === 'xls' || type === 'xlsx') {
return require("../../../assets/file/excel.png");
} else if (type === 'png' || type === 'gif' || type === 'jpg' || type === 'jpeg') {
if (item.local === true) {
return item.file_path;
} else {
return getImageUrl(item.file_path);
}
} else if (type === 'pdf' || type === 'PDF') {
return require("../../../assets/file/pdf.png");
} else if (type === 'ppt' || type === 'PPT' || type === 'pptx') {
return require("../../../assets/file/ppt.png");
} else if (type === 'txt' || type === 'TXT') {
return require("../../../assets/file/txt.png");
} else if (type === 'zip' || type === 'ZIP' || type === 'rar') {
return require("../../../assets/file/zip.png");
}
return require("../../../assets/file/unknown.png");
},
filepreview(url) {
openFile(getImageUrl(url));




},
clickRightMenu(key) {
if (key === 'delete') {
AjaxHttp('POST', 'm=log&a=log_delete', { id: this.$route.params.id }, this, function(res, wself, success) {
if (success) {
wself.deleteMainList()
wself.$vux.toast.text(res.info, 'top');
wself.$router.go(-1);
}
});
} else if (key === 'edit') {
this.$router.push({
name: 'createlog',
params: {
type: this.logDetail.category_id,
action: 'edit'
}
});
}
},
getPraiseImage() {
if (this.logDetail.is_praised === 1) {
this.praiseImage = require("../../../assets/common_praise_light_selected.png");
} else {
this.praiseImage = require("../../../assets/common_praise_light_normal.png");
}
},
onNavClick(index) {
if (index === 0) {
this.logDetail.nav_type = 'reply';
this.nav_type = 'reply';
} else if (index === 1) {
this.logDetail.nav_type = 'praise';
this.nav_type = 'praise';
}




},
praise() { //
if (this.logDetail === '') { return; }
AjaxHttp('POST', 'm=log&a=praise', { id: this.$route.params.id }, this, function(res, wself, success) {
if (success) {
wself.logDetail.is_praised = res.data.is_praise;
wself.$set(wself.logDetail, 'praise_count', res.data.number === '0' ? '' : '' + '' + res.data.number + '');
wself.$set(wself.logDetail, 'b_praise_count', res.data.number === '0' ? '' : res.data.number);
wself.getPraiseImage();
}
});
},
deleteComment(item, index, childindex, type) {
AjaxHttp('POST', 'm=log&a=replydel', { talk_id: item.talk_id }, this, function(res, wself, success) {
if (success) {
var item = wself.logDetail.comment_list[index];
if (type === 'child') {
item.comment_list_child.splice(childindex, 1);
} else {
wself.logDetail.comment_list.splice(index, 1);
}
}
});
},
addComment(item, type) {
if (this.logDetail === '') { return; }
if (type === 'child') {
this.$router.push({ //
name: 'createcomment',
params: {
type: 'comment',
id: item.talk_id,
receiveid: item.creator_child.role_id,
name: item.creator_child.full_name,
}
})
} else if (type === 'big') { //
this.$router.push({
name: 'createcomment',
params: {
type: 'comment',
id: item.talk_id,
receiveid: item.send_role_id,
name: item.user_name,
}
})
} else { //
this.$router.push({
name: 'createcomment',
params: {
type: 'defalut',
id: this.logDetail.log_id,
receiveid: '0',
name: this.logDetail.user_name,
}
})
}
},
enterUser(item) {
this.$router.push({
name: 'user',
params: {
id: item.role_id
}
})
},
deleteMainList() { //
var index = -1;
for (var i = 0; i < this.logListInfo.listItem.length; i++) {
var item = this.logListInfo.listItem[i];
if (item.id === this.$route.params.id) {
index = i;
}
}

if (index >= 0) {
this.logListInfo.listItem.splice(index, 1);
this.$store.commit('updateLogListInfo', this.logListInfo);
}
},
}
};




